.content {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.searchFood {
  height: 30vh;
  width: 55vw;
  background-color: rgba(0, 0, 0, 0.568);

  h2,
  h4 {
    color: white;
    font-weight: bold;
  }
  h2 {
    padding-top: 10px;
   
    margin-left:20px;
  }
  h4 {
    margin-left:40px;
    font-size: 18px;
    
  }
  .searchTool {
    width: 40vw;
    height: 8vh;
    display: flex;
    margin: auto;

    .search {
      width: 100%;
      height: 100%;
      border-radius: 24px 24px 24px 24px;
      border: none;
      outline: 0px;
      font-size: large;
      font-weight: bold;
      text-align: center;
    }
    .bi {
      position: absolute;
      color: rgb(75, 75, 78) ;
      right:75%;
      top:55%;
      width: 70px;
      height: 50px;
      
   
    }
