   .list{
    background-color: rgba(0, 0, 0, 0.568);
    overflow-y: scroll;
    margin: auto;
    margin-top:10px;
    width: 70%;
    height:auto;
    margin-top: 0;
   }
  .food{
    background-color: inherit;
    width: 428px;
    height: 25%;
    margin:  auto;

    padding: 0;
    padding-top: 22px;
    text-align: center;
  
   

    a,a:focus,a:visited,a:hover{
      text-decoration: none;
      color:rgb(253, 253, 253);
      font-weight: bold;
      font-size:19px;
      height: 1200px;
      text-transform: uppercase;
