.content {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;

  .formulario {
    position: relative;
    height: 75%;
    width: 85%;
    background-color: rgba(0, 0, 0, 0.568);
    border: 2px solid white;
    display: flex;
    align-self: center;
    justify-self: center;

    .info {
      width: 50%;
      .title {
        width: 40%;
        margin: 0;
        left: 0px;
        color: rgb(247, 241, 241);
        border-bottom: 4px solid white;
        text-indent: 20%;
      }
      .text {
        position: absolute;
        color: white;
        font-size: 25px;
        margin-top: 50px;
        width: 42%;
        height: 80%;
        line-height: 40px;
        word-spacing: 10px;
        margin-left: 20px;
        text-align: left;
      }
    }
    .form {
      width: 50%;
      display: flex;
      justify-content: center;
      form {
        // border-left: 12px solid rgba(243, 238, 238, 0.164);
        justify-items: center;
        width: 50%;
        height: 100%;
        position: relative;
        div {
          margin-top: 6%;
          display: grid;
          justify-content: center;

          label {
            color: white;
            font-weight: bold;
            font-size: 22px;
            margin-top: 5px;
          }

          
          textarea {
            resize: none;
            height: 250px;
            width: 400px;
            border-radius: 22px;
            border: none;
            font-weight: bold;
            font-size: 18px;
            text-indent: 22px;
            textarea:focus {
              outline: none;
            }
            textarea::placeholder {
              margin-top: 20px;
            }
          }
        }
      }
    }
  }

  

    
.input{

  background-color: red;
  width: 400px;
  height: 40px;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  font-size: 18px;
  text-indent: 22px;


  input[type="text"]  {
    
    outline: none;
    margin: 2% 10%;
  }
  input[type="submit"] {
    color: rgb(255, 255, 255);
    font-weight: bold;
    font-size: 18px;
  }
  input:focus {
    outline: none;
  }
}
}

