.content {
  height: 100vh;
  width: 100vw;
  display: flex;

  .aside {
    width: 20%;
    height: 75%;
    background-color: rgba(0, 0, 0, 0.568);
    position: relative;
    margin-left: 80px;

    .title {
      padding-top: 10px;
      width: 100%;
      color: rgb(247, 241, 241);
        border-bottom: 4px solid white;
        text-indent: 20%;
      
    }
    .options {
      border-color: 20px;
      width: 100%;
      height: 80%;
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          margin: 17%;
          color: white;
          font-size: 22px;
          border-bottom: 2px solid white;
          a:link,
          a:visited,
          a:active {
            text-decoration: none;
            color: white;
            font-size: 22px;
            font-weight: bolder;
          }
          a:hover {
            animation: borde 0.4s;
          }
        }
      }
    }
  }
  .Guide {
    width: 60%;
    height: 75%;
    background-color: rgba(0, 0, 0, 0.568);
    position: relative;
    margin-left: 20px;
    display: block;
    overflow-y: scroll;
    scroll-behavior: smooth;

    .Option {
      width: 100%;
      height: 150%;
      position: relative;
      margin: 0;
      .title {
        width: 100%;
        padding-top:2%;
        width: 50%;
        margin-bottom: 2%;
        color: rgb(247, 241, 241);
        border-bottom: 4px solid white;
        text-indent: 20%;
      }
      .text {
        left: 20px;
        width: 95%;
        height: auto;
        margin: 0;
        color: white;
        font-size: 24px;
        margin-top: 50px;
        line-height: 40px;
        word-spacing: 10px;
        margin-left: 20px;
        text-align: left;

        p {
          margin: 0;
          width: 100%;
          // font-size: 18px;
          // margin-left: 1%;
          font-size: 0.9em;
          font-weight: bold;
        }
      }
    }
  }
}
